import React from "react";

const CommentsList = ({ comments }) => {
  return comments.map((comment, key) => {

    return (
      <>
        <h3>Comments:</h3>
        <div key={`c${key}`} className="comment">
          <h4>{comment.username}</h4>
          <p>{comment.text}</p>
        </div>
      </>
    );
  });
};

export default CommentsList;
